.logo-image {
  width: 8vw;
  border-radius: 25px;
}
@media screen and (min-width: 1408px) {
  .logo-image {
    width: 6vw;
  }
}

@media screen and (max-width: 1024px) {
  .logo-image {
    width: 9vw;
  }
}

@media screen and (max-width: 769px) {
  .logo-image {
    width: 13vw;
  }
}



